import React, { useContext } from 'react';
import { AppContext } from '../../AppContext';

export default function ScrollToTopButton({fillCircle, fillArrow}) {
    const { isLG } = useContext(AppContext);

    const handleClik = () => {
        const app = document.getElementById('app');
        app.scrollTo({
          top: 0,
          left: 0,
          behavior: 'smooth',
        });
    }
    return (
        <div onClick={handleClik} className="go-up">
            <svg       
                width={isLG ? '32px' : '40px'}
                height={isLG ? '32px' : '40px'}
                viewBox="0 0 32 32" 
                xmlns="http://www.w3.org/2000/svg"
            >
            <rect width="32" height="32" rx="16" fill={`${fillCircle}`}/>
            <g clipPath="url(#clip0_309_3922)">
            <path fillRule="evenodd" clipRule="evenodd" d="M23.6692 19.8988C23.1949 20.423 22.3854 20.4634 21.8612 19.9892L16 14.6862L10.1388 19.9892C9.61457 20.4634 8.80513 20.423 8.33084 19.8988C7.85656 19.3746 7.89703 18.5651 8.42124 18.0908L15.1412 12.0108C15.6288 11.5697 16.3712 11.5697 16.8588 12.0108L23.5788 18.0908C24.103 18.5651 24.1434 19.3746 23.6692 19.8988Z" fill={`${fillArrow}`}/>
            </g>
            <defs>
            <clipPath id="clip0_309_3922">
            <rect width="16" height="16" fill="white" transform="translate(8 8)"/>
            </clipPath>
            </defs>
            </svg>
        </div>
    )
}
