import React, { useContext } from 'react';
import './Portfolio.component.scss';
import Card from '../Card/Card.component.jsx';
import Icon from '../Icon/Icon.component.jsx';
import MarkdownParagraph from '../MarkdownParagraph/MarkdownParagraph.component.jsx';
import Sectionh1 from '../SectionH1/Sectionh1.component.jsx';
import Paragraph from '../Paragraph/Paragraph.component.jsx';
import { Link } from 'react-router-dom';
import { AppContext } from '../../AppContext';
import SShape from '../../assets/icons/sShape.svg';
import Container from '../Container/Container.component';

export default function Portfolio() {
  const {
    isLG,
    portfolioData,
    portfolioPageTitleObj,
    portfolioPageIconObj,
    portfolioPageParagraphObj,
    changePortofolioPageId,
  } = useContext(AppContext);

  const setPortfolioPageTitle = portfolioPageTitleObj[1];
  const setPortfolioPageIcon = portfolioPageIconObj[1];
  const setPortfolioPageParagraph = portfolioPageParagraphObj[1];

  return (
    <section className="portfolio" id="services">
      <Icon icon={SShape} class={'s-shape-up'} />
      <Container
        columns={isLG ? `repeat(4, 1fr)` : `repeat(4, 1fr)`}
        rows={isLG ? `repeat(4, 1fr)` : `repeat(2, auto)`}
        maxWidth={`1200px`}
        display={`grid`}
        classe={`lp-container`}
      >
        <div className="section-intro">
          <Sectionh1 h1={'our services'} />
          <MarkdownParagraph fileName={'services.md'} />
          <div
            className="info"
            style={isLG ? { display: 'none' } : { display: 'flex' }}
          >
            <Paragraph
              paragraph={
                'If you don’t see a particular service listed on our site, contact us to see how we can help.'
              }
            />
          </div>
        </div>
        <div
          className="info"
          style={isLG ? { display: 'flex' } : { display: 'none' }}
        >
          <Paragraph
            paragraph={
              'If you don’t see a particular service listed on our site, contact us to see how we can help.'
            }
          />
        </div>
        <div className="cards">
          {portfolioData.map((card, i) => (
            <Link
              key={i}
              to={{
                pathname: `${card.link}`,
              }}
              state={{ from: 'mystate' }}
              onClick={() => {
                setPortfolioPageTitle(card.title);
                setPortfolioPageIcon(card.icon);
                setPortfolioPageParagraph(card.paragraph);
                changePortofolioPageId(card.id);
              }}
              className="card-link"
            >
              <Card id={card.id} icon={card.icon} title={card.title} />
            </Link>
          ))}
        </div>
      </Container>
    </section>
  );
}
